<template>
    <pl-input
            ref="input"
            class="pl-select-input"
            :class="{'pl-multi-select-input':!!multiple}"
            suffixIcon="el-icon-arrow-down"
            clearIcon
            :value="multiple?multipleInputValue: ((isShow&&filterable)?p_inputValue:inputValue)"
            :placeholder="p_placeholder"
            :isFocus="isOpen"
            :clearHandler="onClickClearIcon"
            :inputReadonly="!filterable"

            @input="onInput"
            @click-input="onClickInput"
            @click-suffix-icon="onClickSuffixIcon"
            @keydown.space="onSpace"
            @keydown.enter="onEnter"
            @keydown.up="onUp"
            @keydown.down="onDown"
            @keydown.esc="onEsc"
            @blur="onBlur"
            @focus="onFocus"
    >
        <span v-if="multiple" class="pl-multi-select-input-wrapper">
            <span class="pl-multi-select-input-item pl-multi-select-input-take-over">&nbsp;</span>
            <span class="pl-multi-select-input-item" v-for="(item,index) in multipleData" :key="index">
                <span>{{item.label}}</span>
                <pl-icon icon="el-icon-circle-close" @click.native.stop.prevent="onClickItemCloseIcon(item,index)"/>
            </span>
        </span>
    </pl-input>
</template>

<script>


    import {EmitMixin} from "../../utils/EmitMixin";
    import {EditMixin} from "../../utils/mixins";

    export default {
        name: "pl-select",
        mixins: [EmitMixin, EditMixin],
        provide() {
            return {
                rParentEditor: this,
            }
        },
        props: {
            placeholder: {type: String},                                //

            value: {},                                                  //
            multiple: {type: Boolean},                                  //

            data: {type: Array},                                        //
            labelKey: {type: String},                                   //key
            valueKey: {type: String},                                   //key

            multipleLimit: {type: Number, default: 0},                  //
            filterable: {type: Boolean, default: true},                 //ieieinput pl-select input 
            filterMethod: {type: Function},                             //
            noMatchText: {type: String, default: ''},          //
            noDataText: {type: String, default: ''},               //
        },
        watch: {
            value(val) {
                this.p_value = val
            },
        },
        emitters: {
            emitInput: 'input',
            emitSpace: 'inputspace',
            emitBlur: '',
            emitFocus: '',
        },
        data() {
            return {
                p_value: this.value,                                    //propsvalue
                p_placeholder: this.placeholder,                        //propsplaceholder
                p_inputValue: null,                                     //input
                p_blurTimer: 0,                                         //select itemblur
                p_focusTimer: 0,                                        //select iteminputfocus

                p_select: null,                                         //select
                p_selectOption: {                                       //selectoption
                    data: () => this.p_selectData,
                    value: () => this.p_value,
                    reference: () => this.$refs.input,
                    noDataText: () => this.p_noDataText,
                    render: this.$scopedSlots.default,
                    labelKey: 'label',
                    valueKey: 'value',
                    keyboard: false,
                    autoClose: false,
                    beforeHide: () => {
                        if (!this.multiple) {
                            this.p_placeholder = this.placeholder || ''
                            this.p_inputValue = null
                        }
                    },
                    beforeShow: () => {
                        if (!this.multiple) {
                            this.p_placeholder = this.inputValue
                        }
                    },
                    onItemMousedown: () => {
                        this.p_blurTimer++
                        this.p_focusTimer++
                    },
                    onClick: (item) => {
                        let value;
                        if (!this.multiple) {
                            value = item.value
                        } else {
                            value = this.p_value || []
                            let index = value.indexOf(item.value)
                            if (index > -1) {
                                value.splice(index, 1)
                            } else {
                                if (!!this.multipleLimit && value.length >= this.multipleLimit) return
                                value.push(item.value)
                            }
                            value = [...value]
                        }

                        this.p_value = value
                        this.$refs.input.focus()
                        this.emitInput(this.p_value)

                        if (!this.multiple) {
                            this.hide()
                        }
                    }
                },
            }
        },
        created() {
            /*selectinput*/
            this.$on('blur', () => {
                this.$refs.input.p_value = !!this.multiple ? this.multipleInputValue : this.inputValue
            })
        },
        mounted() {
            this.p_selectOption.render = !this.$scopedSlots.default ? null : (h, item, index) => {
                return this.$scopedSlots.default({
                    ...item,
                    data: item.data.data,
                    index,
                })
            }
        },
        computed: {
            /**
             * labelvalue
             * @author  
             * @date    2020/1/28 10:54
             */
            p_data() {
                if (!this.data || this.data.length === 0) return null
                return this.data.map(d => {
                    let label = !!this.labelKey ? d[this.labelKey] : d;
                    let value = !!this.valueKey ? d[this.valueKey] : d
                    return {label, value, data: d}
                })
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:55
             */
            p_selectData() {
                let data = this.p_data
                if (!data || data.length === 0) return null
                if (!this.p_inputValue) return data
                return data.filter((item, index) => {
                    if (!!this.filterMethod) {
                        return this.filterMethod(this.p_inputValue, item, index)
                    } else {
                        const label = item.label
                        return label.toLowerCase().indexOf(this.p_inputValue.toLowerCase()) > -1
                    }
                })
            },
            p_noDataText() {
                if (!this.p_data) return this.noDataText
                if (!this.p_selectData || this.p_selectData.length === 0) return this.noMatchText
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:55
             */
            inputValue() {
                let data = this.p_data
                if (!data || data.length === 0) {
                    return ''
                }
                for (let i = 0; i < data.length; i++) {
                    const item = data[i];
                    if (this.p_value == item.value) return item.label
                }
                return ''
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:55
             */
            multipleData() {
                if (!this.p_value) return null
                if (this.$plain.utils.typeOf(this.p_value) !== 'array') {
                    console.error('The value of multiple select should be array')
                    return
                }
                if (!this.p_data || this.p_data.length === 0) return
                return this.p_data.filter(d => this.p_value.indexOf(d.value) > -1)
            },
            /**
             * input
             * @author  
             * @date    2020/1/28 11:03
             */
            multipleInputValue() {
                if (!this.multipleData) return ''
                return this.multipleData.map(item => item.label).join('')
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:55
             */
            isShow() {
                if (!this.p_select) return false
                return this.p_select.isShow()
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:55
             */
            isOpen() {
                if (!this.p_select) return false
                return this.p_select.isOpen()
            },
        },
        methods: {
            /*---------------------------------------methods-------------------------------------------*/
            /**
             * 
             * @author  
             * @date    2020/1/28 10:55
             */
            async show() {
                if (!this.isEditable) return
                await this.checkSelect()
                this.p_select.show()
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:56
             */
            async hide() {
                await this.checkSelect()
                this.p_select.hide()
            },
            /**
             * | 
             * @author  
             * @date    2020/1/28 10:56
             */
            async toggle() {
                await this.checkSelect()
                if (this.isShow) {
                    this.hide()
                } else {
                    this.show()
                }
            },


            /*---------------------------------------utils-------------------------------------------*/
            /**
             * p_select
             * @author  
             * @date    2020/1/28 10:56
             */
            async checkSelect() {
                if (!this.p_select) this.p_select = await this.$plain.$select.newSelect(this.p_selectOption)
            },

            /*---------------------------------------event handler-------------------------------------------*/
            /**
             * 
             * @author  
             * @date    2020/1/28 10:56
             */
            onClickClearIcon() {
                this.p_value = null
                this.emitInput(this.p_value)
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:56
             */
            async onClickSuffixIcon() {
                await this.show()
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:57
             */
            async onInput(val) {
                this.p_inputValue = val
                if (!this.isShow && !this.$plain.utils.ie) {
                    this.show()
                }
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:57
             */
            async onClickInput() {
                await this.toggle()
            },
            /**
             * space
             * @author  
             * @date    2020/1/28 10:57
             */
            onSpace(e) {
                if (this.multiple && this.isShow) {
                    e.stopPropagation()
                    e.preventDefault()
                    this.p_select.selectCurrentItem()
                }
                this.emitSpace(e)
            },
            /**
             * enter
             * @author  
             * @date    2020/1/28 10:57
             */
            async onEnter(e) {
                e.stopPropagation()
                e.preventDefault()
                if (this.isShow) {
                    this.p_select.selectCurrentItem()
                } else {
                    await this.show()
                }
            },
            /**
             * up
             * @author  
             * @date    2020/1/28 10:57
             */
            onUp(e) {
                e.stopPropagation()
                e.preventDefault()
                if (!!this.p_select) {
                    this.p_select.prev()
                }
            },
            /**
             * down
             * @author  
             * @date    2020/1/28 10:57
             */
            onDown(e) {
                e.stopPropagation()
                e.preventDefault()
                if (!!this.p_select) {
                    this.p_select.next()
                }
            },
            /**
             * esc
             * @author  
             * @date    2020/1/28 10:57
             */
            onEsc(e) {
                e.stopPropagation()
                e.preventDefault()
                if (this.isShow) {
                    this.hide()
                }
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:57
             */
            async onBlur(...args) {
                if (this.p_blurTimer > 0) {
                    this.p_blurTimer--
                } else {
                    this.emitBlur(...args)
                    if (!!this.isShow) {
                        this.hide()
                    }
                }
            },
            /**
             * 
             * @author  
             * @date    2020/1/28 10:57
             */
            onFocus(...args) {
                if (this.p_focusTimer > 0) {
                    this.p_focusTimer--
                } else {
                    this.emitFocus(...args)
                }
            },
            /**
             * 
             * @author  
             * @date    2020/1/29 8:53
             */
            onClickItemCloseIcon(item, index) {
                index = this.p_value.indexOf(item.value)
                if (index > -1) {
                    this.p_value.splice(index, 1)
                    this.p_value = [...this.p_value]
                }
                this.emitInput(this.p_value)
            },
        },
    }
</script>

