import {EmitMixin} from "../utils/EmitMixin";

export default {
    install(Vue) {

        const $plain = Vue.prototype.$plain || {}
        Vue.prototype.$plain = $plain

        // 
        Vue.mixin({
            created() {
                if (!this.$options.parent) {
                    $plain.$root = this
                }
            }
        })

        $plain.EmitMixin = EmitMixin
        $plain.nextTick = () => new Promise(resolve => Vue.prototype.$nextTick(resolve))

        /**
         * vue
         * @author  
         * @date    2020/3/5 11:56
         * @param   component:string|object             
         * @param   el:HTMLElement                      domdiv
         * @param   parent:vnode                         $router,$store provide $root 
         * @param   parentNode                          dom document.body
         */
        $plain.newInstance = (component, {el, parent, parentNode} = {}) => {

            parentNode = parentNode || document.body
            el = el || document.createElement('div')
            parent = parent || $plain.$root

            if (!el.parentNode) {
                parentNode.appendChild(el)
            }

            return (new Vue({
                el,
                parent,
                render(h) {
                    return h(component)
                },
            }).$mount()).$children[0]
        }
        $plain.log = (...args) => {
            console.log(...args)
        }

    }
}