export interface SelectOption {
    value: any                                                  // 
    data: Array<string>                                         // 
    private?: Boolean                                           // 
    reference: HTMLElement                                      // 
    popoverProps: any                                           // popover
    labelKey: string                                            // key
    valueKey: string                                            // key
    render: Function                                            // key

    autoClose: boolean                                          // 
    closeAfterBody: boolean                                     // reference  popper
    keyboard: boolean                                           // select

    onClick: Function                                           // 
    onShow: Function                                            // 
    onHide: Function                                            // 
    onClickBody: Function                                       // referencepopperbody
    onOpen: Function                                            // 
    onClose: Function                                           // 
    onItemMousedown: Function                                   // mousedown

    beforeShow: Function                                        // 
    beforeHide: Function                                        // 
}

export default class Select {

    ins = null                                                   // select service item 

    constructor(public opt: SelectOption, public controller) {
        opt.autoClose = opt.autoClose === undefined ? true : opt.autoClose
        opt.closeAfterBody = opt.closeAfterBody === undefined ? true : opt.closeAfterBody
        opt.keyboard = opt.keyboard === undefined ? true : opt.keyboard
    }

    /*select*/
    async show() {
        if (this.isShow()) return

        if (!this.ins) {
            this.ins = await this.controller.getInstance()
        }
        this.ins.bind(this)
        this.ins.show()
    }

    /*select*/
    hide() {
        if (!this.isShow()) return
        this.ins.hide()
    }

    /*/select*/
    toggle() {
        if (!!this.isShow()) {
            this.hide()
        } else {
            this.show()
        }
    }

    /**/
    prev() {
        if (!!this.ins) {
            this.ins.prev()
        }
    }

    /**/
    next() {
        if (!!this.ins) {
            this.ins.next()
        }
    }

    /**/
    selectCurrentItem() {
        if (!!this.ins) {
            this.ins.selectCurrentItem()
        }
    }

    /**/
    isShow() {
        if (!this.ins) return false
        return this.ins.isShow
    }

    /**/
    isOpen() {
        if (!this.ins) return false
        return this.ins.isOpen
    }
}