<template>
    <span class="pl-dom">
        <span class="pl-dom-content" ref="content">
            <slot></slot>
        </span>
    </span>
</template>

<script>
    export default {
        name: "pl-dom",
        props: {
            value: {type: Boolean, default: true},                          // body
            container: {},                                                  // 
            autoCreateContainer: {type: Boolean},                           //  querySelector  container   container
        },
        data() {
            return {
                contentEl: null,                                            // content

                parentNode: null,                                           // pl-dom  mounted 
                containerNode: null,                                        // 
                commentNode: document.createComment('pl-dom'),        //  parentNode  childNode
                isMoved: false,                                             // containerNode
            }
        },
        watch: {
            value: {
                immediate: true,
                async handler(val) {
                    await this.$plain.nextTick()
                    this.update(!!val)
                },
            },
            async container() {
                this.update(false)
                this.update(true)
            },
        },
        computed: {
            targetContainer() {
                if (!this.container || this.container === 'document.body' || this.container === 'body') return document.body
                if (this.container instanceof window.Node) return this.container
                if (typeof this.container === "string") {
                    const node = document.querySelector(this.container)
                    if (!node) {
                        if (!!this.autoCreateContainer) {
                            const container = document.createElement('div')
                            this.$plain.utils.addClass(container, this.container.replace('.', ''))
                            document.body.appendChild(container)
                            return container
                        } else {
                            console.error(`can'y find node:${this.container}`)
                            return document.body
                        }
                    } else {
                        return node
                    }
                }
            },
        },
        methods: {
            update(val) {
                if (val) {
                    if (!this.isMoved) {
                        // 
                        this.contentEl = this.$refs.content
                        this.containerNode = this.targetContainer

                        this.$el.replaceChild(this.commentNode, this.contentEl)
                        this.containerNode.appendChild(this.contentEl)

                        this.isMoved = true
                    } else {
                        //  do nothing
                    }
                } else {
                    if (!!this.isMoved) {
                        this.containerNode.removeChild(this.contentEl)
                        this.$el.replaceChild(this.contentEl, this.commentNode)

                        this.contentEl = null
                        this.containerNode = null

                        this.isMoved = false
                    }
                }
            },
        },
        beforeDestroy() {
            this.update(false)
        }
    }
</script>

